         url('../fonts/Custom-Font.woff') format('woff'),
         url('../fonts/Custom-Font.svg#custom_font') format('svg');
    font-weight: 400;       // For normal width. It could has another value   
    font-style: normal;     // Also could has another value
    }


// Var for using custom font
$CustomFont: 'CustomFont', Helvetica, Arial, sans-serif;
*/
* {
  box-sizing: border-box;
  position: relative;
}

html, body {
  height: 100%;
  font-family: 'Roboto', sans-serif;
}

.page {
  background-color: #f5f5f5;
}

.wrap {
  max-width: 1440px;
  min-width: 375px;
  width: 100%;
  padding-left: 28px;
  padding-right: 28px;
  padding-bottom: 50px;
  margin: 0 auto;
}

@media (max-width: 680px) {
  .wrap {
    padding-left: 15px;
    padding-right: 15px;
  }
}

.content {
  display: flex;
  flex-direction: column;
  max-width: 592px;
  width: 100%;
  margin: 0 auto;
  margin-bottom: 56px;
  padding-top: 50px;
  text-align: center;
}

@media (max-width: 680px) {
  .content {
    margin-bottom: 40px;
  }
}

@media (max-width: 540px) {
  .content {
    max-width: 343px;
  }
}

.content p {
  margin: 0;
  font-size: 15px;
  font-weight: 300;
  color: rgba(0, 0, 0, 0.54);
}

@media (max-width: 540px) {
  .content p {
    font-size: 11px;
  }
}

.content_title {
  margin: 0;
  margin-bottom: 16px;
  color: rgba(0, 0, 0, 0.7);
  font-size: 24px;
  font-weight: 500;
}

@media (max-width: 680px) {
  .content_title {
    font-size: 18px;
  }
}

.content_link {
  margin-top: 25px;
  font-size: 13px;
  color: #c82021;
  font-weight: 300;
}

@media (max-width: 680px) {
  .content_link {
    font-size: 11px;
  }
}

.place {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
}

.place_img {
  height: 100%;
  overflow: hidden;
  border: 8px solid #ffffff;
}

.place_img img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.place_desc {
  display: flex;
  flex-direction: column;
  margin-top: 12px;
}

.place_title, .place_subtitle {
  font-size: 11px;
}

.place_title {
  color: rgba(0, 0, 0, 0.7);
  font-weight: 500;
}

.place_subtitle {
  margin-top: 4px;
  color: rgba(0, 0, 0, 0.54);
}

.slider {
  max-height: 808px;
}

.slider._mobile {
  display: none;
}

@media (max-width: 680px) {
  .slider._mobile {
    display: block;
  }
}

@media (max-width: 680px) {
  .slider {
    display: none;
    max-height: none;
  }
}

.slider_item {
  display: flex;
  justify-content: space-between;
}

@media (max-width: 680px) {
  .slider_item {
    flex-direction: column;
  }
}

.slider_column {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  column-gap: 12.5%;
  row-gap: 22px;
}

@media (max-width: 1322px) {
  .slider_column {
    column-gap: 8.5%;
  }
}

@media (max-width: 1222px) {
  .slider_column {
    grid-template-columns: 0.95fr 0.95fr;
    grid-template-rows: 0.95fr 0.95fr;
  }
}

@media (max-width: 1122px) {
  .slider_column {
    grid-template-columns: 0.9fr 0.9fr;
    grid-template-rows: 0.9fr 0.9fr;
  }
}

@media (max-width: 1014px) {
  .slider_column {
    grid-template-columns: 0.8fr 0.8fr;
    grid-template-rows: 0.8fr 0.8fr;
    column-gap: 7.5%;
  }
}

@media (max-width: 900px) {
  .slider_column {
    grid-template-columns: 0.75fr 0.75fr;
    grid-template-rows: 0.75fr 0.75fr;
    column-gap: 7.5%;
  }
}

@media (max-width: 850px) {
  .slider_column {
    grid-template-columns: 0.7fr 0.7fr;
    grid-template-rows: 0.7fr 0.7fr;
    column-gap: 7.5%;
  }
}

@media (max-width: 800px) {
  .slider_column {
    grid-template-columns: 0.65fr 0.65fr;
    grid-template-rows: 0.65fr;
    column-gap: 7.5%;
  }
}

@media (max-width: 680px) {
  .slider_column {
    column-gap: 5.5%;
  }
}

.slider_column._big {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  max-width: 642px;
  width: 100%;
  max-height: 100%;
  margin-right: 6%;
}

.slider_column._big:last-child {
  margin-right: 0;
}

.slider_column._big._solo {
  margin: 0 auto;
}

@media (max-width: 1322px) {
  .slider_column._big {
    margin-right: 5%;
  }
}

@media (max-width: 1014px) {
  .slider_column._big {
    margin-right: 4.25%;
    max-width: 472px;
  }
}

@media (max-width: 680px) {
  .slider_column._big {
    max-width: 100%;
    margin-right: 0;
    margin-bottom: 24px;
  }
}

.slider_controls {
  display: flex;
  width: 100%;
  justify-content: center;
  margin-top: 40px;
}

@media (max-width: 680px) {
  .slider_controls {
    margin-top: 32px;
  }
}

.slider_control {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 38px;
  height: 38px;
  margin-right: 32px;
  border-radius: 2px;
  background-color: rgba(0, 0, 0, 0.7);
  border-radius: 2px;
  cursor: pointer;
  user-select: none;
  transition: 0.3s;
}

.slider_control:hover svg {
  transform: translateX(-2px);
}

.slider_control:last-child {
  margin-right: 0;
}

.slider_control._next {
  transform: rotate(180deg);
}

.slider_control svg {
  width: 13px;
  height: 13px;
  transition: 0.3s;
}

.slider_control.swiper-button-disabled {
  opacity: 0.7;
}

.slider_control.swiper-button-disabled:hover svg {
  transform: translateX(0);
}
